import { Component, OnInit } from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {Celular, CelularesService} from '../../servicios/celulares.service';

@Component({
  selector: 'app-celular',
  templateUrl: './celular.component.html',
})
export class CelularComponent implements OnInit {

  celular: Celular;
  celulares: Celular[] = [];
  constructor(private activatedRoute: ActivatedRoute,
              private celularesService: CelularesService,
              private router: Router) {
    this.activatedRoute.params.subscribe( params => {
      this.celular = this.celularesService.getCelular( params['id'] );
    });
    this.celulares = this.celularesService.getCelulares(this.celular.marca, this.celular.id);
  }

  ngOnInit() {

  }
  verCelular(idx: string) {
    this.celulares = this.celularesService.getCelulares(this.celular.marca, idx);
    this.router.navigate(['/celular', idx]);
    window.scroll(0, 0);
  }

}
