import { Component, OnInit } from '@angular/core';
import { CelularesService, Celular } from '../../servicios/celulares.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-celulares',
  templateUrl: './celulares.component.html',
})
export class CelularesComponent implements OnInit {

  celularesApple: Celular[] = [];
  celularesSamsung: Celular[] = [];
  celularesXiaomi: Celular[] = [];
  celularesMotorola: Celular[] = [];
  // tslint:disable-next-line:variable-name
  constructor( private celularesService: CelularesService,
               private router: Router) {
  }

  ngOnInit() {
    this.celularesApple = this.celularesService.getCelularesApple();
    this.celularesSamsung = this.celularesService.getCelularesSamsung();
    this.celularesXiaomi = this.celularesService.getCelularesXiaomi();
    this.celularesMotorola = this.celularesService.getCelularesMotorola();
    // console.log(this.celulares);
  }

  verCelular(idx: string) {
    window.scroll(0, 0);
    // console.log(idx);
    this.router.navigate(['/celular', idx]);
  }
}
